import React, { useEffect, useState } from 'react'
import { useGun } from '../api/gunContext'

import style from './Read.module.css'


export default function Reader({ book }) {
  const gun = useGun()
  
  const [delay, setDelay] = useState(book.readDelay || 1000)
  const [position, setPosition] = useState(book.readPosition || 0)
  const [totalTime, setTotalTime] = useState(book.readTime || 0)
  
  const [words, setWords] = useState([])
  const [playing, setPlaying] = useState(false)
  
  const word = words[position] || ''
  const wordBefore = words[position - 1] || ''
  const wordAfter = words[position + 1] || ''
  
  
  const adjustDelay = (amount) => {
    setDelay((prev) => Math.min(Math.max(prev + amount, 50), 2500))
  }
  
  
  const adjustPosition = (amount) => {
    setPosition((prev) => Math.min(Math.max(prev + amount, 0), words.length - 1))
  }
  
  
  useEffect(() => {
    if (book?.text) {
      const textWords = book.text.replace(/-/g, '- ').split(/\s+/)
      setWords(textWords)
      setPosition((prev) => Math.min(prev, textWords.length - 1))
    }
  }, [book])
  
  
  useEffect(() => {
    let timer
    if (playing) {
      if (position < words.length - 1) {
        timer = setTimeout(() => {
          setPosition(position + 1)
          setTotalTime(totalTime + delay)
        }, delay)
      } else {
        setPlaying(false)
      }
    }
    
    return () => clearTimeout(timer)
  }, [playing, position, delay, words.length])
  
  
  useEffect(() => {
    if (delay) gun.get('books').get(book?.id).get('readDelay').put(delay)
  }, [delay])
  
  
  useEffect(() => {
    if (position) gun.get('books').get(book?.id).get('readPosition').put(position)
  }, [position])
  
  
  useEffect(() => {
    if (totalTime) gun.get('books').get(book?.id).get('readTime').put(totalTime)
  }, [totalTime])
  
  
  return <div className={style.container}>
    <div className={style.title}>{book?.title}</div>
    
    <table className={style.words}>
      <tbody>
        <tr>
          <td className={style.prior}>{wordBefore}</td>
          <td className={style.current}>{word}</td>
          <td className={style.subsequent}>{wordAfter}</td>
        </tr>
      </tbody>
    </table>
    
    <div className={style.controls}>
      <button onClick={() => adjustDelay(50)}>Slower</button>
      <button onClick={() => setPlaying((p) => !p)}>{playing ? 'Stop' : 'Play'}</button>
      <button onClick={() => adjustDelay(-50)}>Faster</button>
    </div>
    
    <div className={style.positionControls}>
      <button onClick={() => adjustPosition(-1)}>&larr;</button>
      <input
        type="range"
        min="0"
        max={words.length - 1}
        value={position}
        onChange={(e) => setPosition(Number(e.target.value))}
      />
      <button onClick={() => adjustPosition(1)}>&rarr;</button>
    </div>
    
    <table className={style.stats}>
      <thead>
        <tr>
          <th>Delay</th>
          <th>Position</th>
          <th>Total Time</th>
        </tr>
      </thead>
      
      <tbody>
        <tr>
          <td>{delay}ms</td>
          <td>{position} / {words.length - 1}</td>
          <td>{Math.round(totalTime/1000)}s</td>
        </tr>
      </tbody>
    </table>
  </div>
}