import {useEffect, useState} from 'react'
import {useGun} from 'api/gunContext'
import {useUi} from 'api/uiContext'

import style from './Editor.module.css'


export default function Editor({book}) {
  const gun = useGun()
  
  const [title, setTitle] = useState('')
  const [author, setAuthor] = useState('')
  const [description, setDescription] = useState('')
  const [image, setImage] = useState('')
  const [url, setUrl] = useState('')
  const [text, setText] = useState('')
  
  const {setEditSelection, setReadSelection} = useUi()
  
  
  const handleSubmit = (e)=>{
    e.preventDefault()
    console.log('saving', book?.id)
    
    const thisn = {
      title,
      author,
      description,
      image,
      url,
      text
    }
    
    console.log('thisn', thisn)
    gun.get('books').get(book?.id).put(thisn)
    
    setEditSelection(null)
    
    return false
  }
  
  
  const readIt = ()=>{
    setReadSelection(book)
    setEditSelection(null)
  }
  
  
  const resetForm = ()=>{
    setTitle('')
    setAuthor('')
    setDescription('')
    setImage('')
    setUrl('')
    setText('')
  }
  
  
  useEffect(()=>{
    if (book) {
      setTitle(book?.title || '')
      setAuthor(book?.author || '')
      setDescription(book?.description || '')
      setImage(book?.image || '')
      setUrl(book?.url || '')
      setText(book?.text || '')
    } else {
      resetForm()
    }
  }, [book])
  
  
  if (!book) return null
  
  return <div>
    <div className={style.container}>
      <label>Editing</label>
      <span>{book?.id}</span>
      
      {book?.readPosition && <>
        <label>Current Position</label>
        <span>{book.readPosition}/{book.text.length - 1}</span>
      </>}
      
      {book?.readDelay && <>
        <label>Read Speed</label>
        <span>{book.readDelay}ms</span>
      </>}
      
      {book?.readTime && <>
        <label>Read Time</label>
        <span>{book.readTime/1000}s</span>
      </>}
    </div>
    
    <hr/>
    
    <form
      className={style.form}
      onSubmit={handleSubmit}
    >
      <label htmlFor="edit-title"> Title </label>
      <input id="edit-title" required type="text" value={title} onChange={e=>setTitle(e.target.value)} />
      
      <label htmlFor="edit-text"> Text </label>
      <textarea id="edit-text" className={style.content} value={text} onChange={e=>setText(e.target.value)} />
      
      <label htmlFor="edit-author"> Author </label>
      <input id="edit-author" type="text" value={author} onChange={e=>setAuthor(e.target.value)} />
      
      <label htmlFor="edit-description"> Description </label>
      <textarea id="edit-description" value={description} onChange={e=>setDescription(e.target.value)} />
      
      <label htmlFor="edit-image"> Image </label>
      <input id="edit-image" type="text" value={image} onChange={e=>setImage(e.target.value)} />
      
      <label htmlFor="edit-url"> URL </label>
      <input id="edit-url" type="text" value={url} onChange={e=>setUrl(e.target.value)} />
      
      <span />
      
      <div>
        <button type="submit">Save</button>
      </div>
    </form>
  </div>
}