import React, { createContext, useContext, useEffect, useMemo, useState } from 'react'
import Gun from 'gun'


const GunContext = createContext(null)


export const GunProvider = ({ children }) => {
  // Initialize Gun once
  const gun = useMemo(() => {
    return Gun({
      peers: [],
    })
  }, [])
  
  window.gun = gun
  
  return (
    <GunContext.Provider value={gun}>
      {children}
    </GunContext.Provider>
  )
}


export const useGun = () => {
  return useContext(GunContext)
}


export const useGunVar = (key, defaultValue) => {
  const gun = useGun()
  const [value, setValue] = useState(defaultValue)
  
  const setter = value=>{
    gun.get(key).put(value)
  }
  
  
  useEffect(() => {
    const subscription = gun.get(key).on(value => {
      setValue(value)
    })
    
    return () => subscription.off()
  }, [gun, key])
  
  
  return [value, setter]
}