import React, {useEffect, useState} from 'react'
import { useGun } from 'api/gunContext'
import { useUi } from 'api/uiContext'
import CHAR from 'unicode-icons'

import style from './List.module.css'


export default function() {
  const gun = useGun()
  
  const [books, setBooks] = useState([])
  
  const { setEditSelection, setReadSelection } = useUi()
  
  
  useEffect(()=>{
    // get all root elements from Gun
    gun.get('books').map().on((data, id) => {
      if (data) {
        setBooks(books => {
          const existingBook = books.find(b => b.id === id)
          if (existingBook) return books
          return [...books, {...data, id}]
        })
      }
    })
  }, [gun])


  useEffect(()=>{
    setEditSelection(null)
    setReadSelection(null)
  }, [])
  
  
  function BookUI({book}){
    const readMe = ()=>{
      setReadSelection(book)
      setEditSelection(null)
    }
    
    const editMe = ()=>{
      setEditSelection(book)
      setReadSelection(null)
    }
    
    const deleteMe = ()=>{
      if (window.confirm(`Are you sure you want to delete ${book?.title}?`)) {
        console.log('deleting', book?.id)
        gun.get('books').get(book?.id).put(null)
      }
    }
    
    return <tr>
      <td onClick={readMe}>
        {book?.title}
      </td>
      
      <td onClick={editMe}>
        {CHAR.PENCIL}
      </td>
      
      <td onClick={deleteMe}>
        {CHAR.RED_X}
      </td>
    </tr>
  }
  
  
  return <>
    {books?.map && <table className={style.container}>
      <tbody>
        {books.map((book, index) => <BookUI book={book} key={index} />
        )}
      </tbody>
    </table>}
  </>
}