import React, {useState, useEffect} from 'react'
import { useUi } from 'api/uiContext'
import Editor from 'com/Editor'
import List from 'com/List'
import Read from "com/Read"
import {v4 as uuid} from 'uuid'


export default function Main() {
  const {
    editSelection,
    readSelection,
    setEditSelection,
    setReadSelection,
  } = useUi()
  
  const [showBookList, setShowBookList] = useState(false)
  
  
  const addBook = ()=>{
    setEditSelection({
      id: uuid(),
    })
  }
  
  
  useEffect(()=>{
    setShowBookList(false)
  }, [editSelection, readSelection])
  
  
  return <>
    <h1>rapt</h1>
    
    <button onClick={()=>setShowBookList(!showBookList)}>
      {showBookList ? 'Hide Book List' : 'Show Book List'}
    </button>
    
    {showBookList && <div className="vertical flex container">
      <List />
    </div>}
    
    <button onClick={addBook}>Add Book</button>
    
    {editSelection && <Editor book={editSelection} />}
    
    {readSelection && <Read book={readSelection} />}
    
    {readSelection && <button onClick={()=>setReadSelection(null)}>Done Reading</button>}
  </>
}
